<template>
    <div class="layout">
        <router-view></router-view>
    </div>
</template>
<script>
    import card from './components/card'
    export default {
        data () {
            return {
                isCollapsed: false
            }
        },
        components: {
            card
        },
        computed: {
            rotateIcon () {
                return ['menu-icon', this.isCollapsed ? 'rotate-icon' : '']
            },
            menuitemClasses () {
                return ['menu-item', this.isCollapsed ? 'collapsed-menu' : '']
            }
        },
        methods: {
            collapsedSider () {
                this.$refs.side1.toggleCollapse()
            }
        }
    }
</script>
<style lang="less">
    
    .top-key-name {
        padding-top: 24px;
        line-height: 1;
        padding-bottom: 12px;
    }
    .card_title {
        height: 50px;
        line-height: 50px;
        padding-left: 24px;
        font-size: 16px;
        color: #000;
        border-bottom:1px solid #e8e8e8;
    }
    .layout{
        background: #f5f7f9;
        position: absolute;
        overflow: hidden;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        .page-view {
            .page_breadcrumb {
                padding: 12px 24px;
            }
            .page_content {
                margin: 0 24px;
                background: #fff;
                padding-bottom: 50px;
            }
            &.full_page {
                height: 100%;
                display: flex;
                flex-direction: column;
                .page_content {
                    flex: 1;
                }
            }
        }
        .ivu-card-bordered {
            border: none;
        }
        .ivu-card {
            border-radius: 0;
        }
    }
</style>



