<template>
    <div class="top-bar">
        <div class="top-btn left-control" @click="hiddenMenu">
            <Icon type="navicon-round" size="30"></Icon>
        </div>
        <!-- <div class="top-btn2 left-control menu-control" @click="openLeftMenu">
            <Icon type="navicon-round" size="30"></Icon>
        </div> -->
        <div class="right-center" style="margin-right:20px;">
            <Poptip class="pick" placement="bottom-end" :transfer="true">
                <div class="user_mess">
                    <Badge count="3">
                        <Icon type="ios-bell-outline" size="26"></Icon>
                    </Badge>
                </div>
                <div class="api" slot="content">
                    <Tabs size="small">
                        <TabPane label="通知 (5)">
                            <ul>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                            </ul>
                        </TabPane>
                        <TabPane label="消息 (3)">
                            <ul>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                            </ul>
                        </TabPane>
                        <TabPane label="待办 (4)">
                            <ul>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                                <li>消息一</li>
                            </ul>
                        </TabPane>
                    </Tabs>
                </div>
            </Poptip>
            <Dropdown class="pick" placement="bottom-end">
                <div class="user-box">
                    <div class="user-photo link">
                        <img src="https://avatars2.githubusercontent.com/u/9925435?v=4&s=400&u=0a2b1bbb9e804619070e9f020f34db30f63d7902">
                    </div>
                    <div>管理员</div>
                </div>
                <DropdownMenu slot="list" class="top_user_bar">
                    <DropdownItem><Icon type="person"></Icon>个人中心</DropdownItem>
                    <DropdownItem><Icon type="ios-gear"></Icon>设置</DropdownItem>
                    <DropdownItem @click="$router.push({parh:'/'})"><Icon type="android-refresh"></Icon>退出登录</DropdownItem>
                </DropdownMenu>
            </Dropdown>
        </div>
    </div>
</template>
<script>
    export default {
        methods: {
            hiddenMenu () {},
            openLeftMenu () {},
            ok () {
                this.$Message.info('点击了确定')
            },
            cancel () {
                this.$Message.info('点击了取消')
            }
        }
    }
</script>
<style lang="less">
    .top-bar {
        display: flex;
        justify-content: space-between;
        height: 64px;
        background: #fff;
        // box-shadow: 0 3px 10px rgba(84, 48, 132, 0.03);
        border-bottom: 1px solid #eee;
        position: relative;
        z-index: 5;
        .left-control {
            display: block;
            width: 64px;
            height: 64px;
            line-height: 64px;
            text-align: center;
            transition: 300ms;
            box-sizing: border-box;
            padding-top: 5px;
            &:hover {
                background: #eee;
            }
        }
        .top-btn2 {
            display: none;
        }
        .user_mess {
            box-sizing: border-box;
            height: 64px;
            padding: 20px 20px 0;
        }
        .user-box {
            padding: 17px;
            display: flex;
            align-items: center;
            line-height: 1;
            .user-photo {
                width: 30px;
                height: 30px;
                margin-right: 10px;
                margin-left: 0;
            }
        }
        .right-center {
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .pick {
            &:hover {
                background-color: #efefef;
            }
        }
        .top_user_bar {
            .ivu-icon {
                margin-right: 10px;
            }
        }
    }
</style>



