<template>
    <div class="page-view">
        <div class="page_breadcrumb">
            <Breadcrumb>
                <BreadcrumbItem to="/">首页</BreadcrumbItem>
                <BreadcrumbItem>搜索列表（文章）</BreadcrumbItem>
            </Breadcrumb>
        </div>
    </div>
</template>


<script>
import axios from 'axios'
export default {
    data () {
        return {
            list: []
        }
    },
    mounted () {
        this.getData()
    },
    methods: {
        getData () {
            axios.get('https://cnodejs.org/api/v1/topics').then(function (response) {
                console.log(response)
            })
            .catch(function (error) {
                console.log(error)
            })
        }
    }
}
</script>



